import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class OrdersService {
    orders: any[];
    api_url = 'https://smartstock.elbanyaoui.com/api/orders';

    myToken = localStorage.getItem('token');

    onOrdersChanged: BehaviorSubject<any>;
    headers = new HttpHeaders({ 'Authorization': 'Bearer ' + this.myToken });



    constructor( private _httpClient: HttpClient ) { }

    getOrders()
    {        const httpOptions = {
            headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + this.myToken })};
        return this._httpClient.get(
            this.api_url,  httpOptions
        );
    }

    setOrder(data)
    {
        const httpOptions = {
        headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + this.myToken })};
        this._httpClient.post(this.api_url, data , httpOptions).subscribe(resp => console.log(resp));
    }

    getStocks()
    {        const httpOptions = {
        headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + this.myToken })};
        return this._httpClient.get(
            this.api_url,  httpOptions
        );
    }


}
