import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Observable} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
    private api_url = 'https://smartstock.elbanyaoui.com/api/';
    constructor(private httpClient: HttpClient) { }

    getToken(user): Observable<Object>
    {
        return this.httpClient.post(this.api_url + 'login', user);
    }

    signIn(): Promise<boolean>
    {
        return new Promise(
            (resolve, reject) => {
                localStorage.setItem('isLogged', 'true');
                resolve(true);
            }
        );
    }

    emptyLocalstorage(): void
    {
        localStorage.setItem('isLogged', 'false');
        localStorage.setItem('token', null);

    }

    isLogged(): boolean
    {
        if ( localStorage.getItem('isLogged') === 'true')
        {
            return true;
        }
        return false;

    }

    signUp(Newuser): Observable<Object>
    {
        return this.httpClient.post(this.api_url + 'register', Newuser);
    }


}
