module.exports = "/**\n * Applies styles for users in high contrast mode. Note that this only applies\n * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n * attribute, however Chrome handles high contrast differently.\n * @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n *    `white-on-black` or `black-on-white`.\n */\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\nfuse-sidebar {\n  display: flex;\n  flex-direction: column;\n  flex: 1 0 auto;\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  overflow-x: hidden;\n  overflow-y: auto;\n  -webkit-overflow-scrolling: touch;\n  width: 280px;\n  min-width: 280px;\n  max-width: 280px;\n  z-index: 1000;\n  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.35);\n  background: white; }\n@media (max-width: 599px) {\n    fuse-sidebar {\n      min-width: 0 !important;\n      max-width: 80vw !important;\n      width: 80vw !important; } }\nfuse-sidebar.left-positioned {\n    left: 0;\n    -webkit-transform: translateX(-100%);\n            transform: translateX(-100%); }\nfuse-sidebar.right-positioned {\n    right: 0;\n    -webkit-transform: translateX(100%);\n            transform: translateX(100%); }\nfuse-sidebar.open {\n    -webkit-transform: translateX(0);\n            transform: translateX(0); }\nfuse-sidebar.locked-open {\n    position: relative !important;\n    -webkit-transform: translateX(0) !important;\n            transform: translateX(0) !important; }\nfuse-sidebar.folded {\n    position: absolute !important;\n    top: 0;\n    bottom: 0; }\nfuse-sidebar.animations-enabled {\n    transition-property: width, min-width, max-width, -webkit-transform;\n    transition-property: transform, width, min-width, max-width;\n    transition-property: transform, width, min-width, max-width, -webkit-transform;\n    transition-duration: 150ms;\n    transition-timing-function: ease-in-out; }\n.fuse-sidebar-overlay {\n  position: absolute;\n  top: 0;\n  bottom: 0;\n  left: 0;\n  right: 0;\n  z-index: 999;\n  background-color: rgba(0, 0, 0, 0.6);\n  opacity: 0; }\n.fuse-sidebar-overlay.fuse-sidebar-overlay-invisible {\n    background-color: transparent; }\n"