module.exports = "/**\n * Applies styles for users in high contrast mode. Note that this only applies\n * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n * attribute, however Chrome handles high contrast differently.\n * @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n *    `white-on-black` or `black-on-white`.\n */\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\n:host {\n  position: relative;\n  display: flex;\n  flex: 0 0 auto;\n  z-index: 4; }\n:host.below {\n    z-index: 2; }\n:host .mat-toolbar {\n    position: relative;\n    background: inherit;\n    color: inherit; }\n:host .logo {\n    display: flex;\n    align-items: center; }\n:host .logo .logo-icon {\n      width: 38px; }\n:host .user-button,\n  :host fuse-search-bar,\n  :host .language-button,\n  :host .chat-panel-toggle-button,\n  :host .quick-panel-toggle-button {\n    min-width: 64px;\n    height: 64px; }\n@media (max-width: 599px) {\n      :host .user-button,\n      :host fuse-search-bar,\n      :host .language-button,\n      :host .chat-panel-toggle-button,\n      :host .quick-panel-toggle-button {\n        height: 56px; } }\n:host .navbar-toggle-button {\n    min-width: 56px;\n    height: 56px; }\n:host .toolbar-separator {\n    height: 64px;\n    width: 1px;\n    background: rgba(0, 0, 0, 0.12); }\n@media (max-width: 599px) {\n      :host .toolbar-separator {\n        height: 56px; } }\n"