import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { MatMomentDateModule } from '@angular/material-moment-adapter';
import { MatButtonModule, MatIconModule } from '@angular/material';
import { TranslateModule } from '@ngx-translate/core';
import 'hammerjs';

import { JwtModule } from '@auth0/angular-jwt';

import { FuseModule } from '@fuse/fuse.module';
import { FuseSharedModule } from '@fuse/shared.module';
import { FuseProgressBarModule, FuseSidebarModule, FuseThemeOptionsModule } from '@fuse/components';

import { fuseConfig } from 'app/fuse-config';

import { AppComponent } from 'app/app.component';
import { LayoutModule } from 'app/layout/layout.module';
import { SampleModule } from 'app/main/sample/sample.module';
import { OrdersModule } from 'app/main/orders/orders.module';
import { LoginModule } from './main/login/login.module';
import {AccountsModule} from './main/accounts/accounts.module';
const appRoutes: Routes = [
    {
        path      : 'login',
        redirectTo: 'login/auth'
    },
    {
        path      : '**',
        redirectTo: 'sample'
    }

];

@NgModule({
    declarations: [
        AppComponent,
    ],
    imports     : [
        BrowserModule,
        BrowserAnimationsModule,
        HttpClientModule,
        RouterModule.forRoot(appRoutes),
        TranslateModule.forRoot(),

        // Material moment date module
        MatMomentDateModule,

        // Material
        MatButtonModule,
        MatIconModule,

        // Fuse modules
        FuseModule.forRoot(fuseConfig),
        FuseProgressBarModule,
        FuseSharedModule,
        FuseSidebarModule,
        // FuseThemeOptionsModule,

        // App modules
        LayoutModule,
        OrdersModule,
        LoginModule,
        AccountsModule,
        SampleModule,
        // JWT
        JwtModule.forRoot({
            config: {
                tokenGetter: function  tokenGetter(): any {
                    if (window.localStorage){
                        return localStorage.getItem('token');
                    }
                    console.log(' Navigator not supported');
                    },
                whitelistedDomains: ['localhost:4200'],
                blacklistedRoutes: ['http://localhost:4200/login/auth']
            }
        })
    ],
    bootstrap   : [
        AppComponent
    ]
})
export class AppModule
{
}
