import {Component, Input, OnInit, ViewChild} from '@angular/core';
import {MatPaginator, MatTableDataSource} from '@angular/material';
import {SelectionModel} from '@angular/cdk/collections';
import {AccountsService} from '../../../services/accounts/accounts.service';



@Component({
  selector: 'app-table-accounts',
  templateUrl: './table-accounts.component.html',
  styleUrls: ['./table-accounts.component.scss']
})
export class TableAccountsComponent implements OnInit {
    showSpinner = true;
    tableSize ;
    // @ts-ignore
    @Input() accountName: string;
    displayedColumns: string[] = ['id', 'name' , 'phone', 'email', 'action'];
    dataSource;
    selection = new SelectionModel<PeriodicElement>(true, []);
    @ViewChild(MatPaginator) paginator: MatPaginator;
     account;
    constructor(private accountsService: AccountsService) { }

  ngOnInit(): void {

          this.accountsService.getListAccounts()
              .subscribe((res : any[])=>{
                 this.account =  res;
                  ELEMENT_DATA = [];
                 for(let i=0 ; i<res.length ; i++){
                     if(res[i].type == this.accountName)
                        ELEMENT_DATA.push({id : res[i].id, uuid:res[i].uuid  ,name: res[i].name, phone: res[i].phone, email: res[i].email, type: res[i].type });
                 }
                 this.dataSource = new MatTableDataSource<PeriodicElement>(ELEMENT_DATA);
                 this.dataSource.paginator = this.paginator;
                  this.showSpinner = false;
                  this.tableSize = ELEMENT_DATA.length;

              });
  }
    /** Whether the number of selected elements matches the total number of rows. */
    isAllSelected() {
        const numSelected = this.selection.selected.length;
        const numRows = this.dataSource.data.length;
        return numSelected === numRows;
    }
    /** Selects all rows if they are not all selected; otherwise clear selection. */
    masterToggle() {
        this.isAllSelected() ?
            this.selection.clear() :
            this.dataSource.data.forEach(row => this.selection.select(row));
    }


    deleteAccount(uuid){
        this.accountsService.deleteAccount(uuid);
        this.ngOnInit();
    }




}
export interface PeriodicElement {
    id: string;
    uuid: string ;
    name: string;
    phone: string;
    email: string;
    type: string;

}


//data to bring from the rest api

let ELEMENT_DATA: PeriodicElement[] = [];



