import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {BehaviorSubject} from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class StocksService {
    api_url = 'https://smartstock.elbanyaoui.com/api/stocks';

    myToken = localStorage.getItem('token');

    headers = new HttpHeaders({ 'Authorization': 'Bearer ' + this.myToken });
  constructor(private httpClient: HttpClient) { }

    getStocks()
    {        const httpOptions = {
        headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + this.myToken })};
        return this.httpClient.get(
            this.api_url,  httpOptions
        );
    }
}
