import { NgModule } from '@angular/core';
import { NewAccountComponent } from './new-account/new-account.component';
import { ListAccountsComponent } from './list-accounts/list-accounts.component';
import { TableAccountsComponent } from './table-accounts/table-accounts.component';
import { DetailAccountComponent } from './detail-account/detail-account.component';
import {RouterModule} from '@angular/router';
import {AccountsComponent} from './accounts.component';
import {
    MatButtonModule,
    MatCheckboxModule,
    MatDatepickerModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatRippleModule, MatSelectModule,
    MatTableModule, MatTabsModule, MatToolbarModule
} from '@angular/material';
import {MatRadioModule} from '@angular/material/radio';
import {MatPaginatorModule} from '@angular/material/paginator';
import {FuseSharedModule} from '../../../@fuse/shared.module';
import { SweetAlert2Module } from '@sweetalert2/ngx-sweetalert2';
import { MatProgressSpinnerModule } from '@angular/material';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';


const routes = [
    {
        path     : 'accounts',
        component: AccountsComponent
    },
    {
        path     : 'accounts/new/:accountName',
        component: NewAccountComponent
    },
    {
        path     : 'accounts/:uuid',
        component: DetailAccountComponent
    }

];

@NgModule({
  imports: [
      RouterModule.forChild(routes),

      MatButtonModule,
      MatCheckboxModule,
      MatDatepickerModule,
      MatFormFieldModule,
      MatIconModule,
      MatInputModule,
      MatMenuModule,
      MatRippleModule,
      MatTableModule,
      MatToolbarModule,
      MatTabsModule,
      MatSelectModule,
      MatRadioModule,
      FuseSharedModule,
      MatPaginatorModule,
      MatProgressSpinnerModule,
      BrowserAnimationsModule,
      SweetAlert2Module.forRoot()

  ],
  declarations: [NewAccountComponent, AccountsComponent, TableAccountsComponent, DetailAccountComponent, ListAccountsComponent]
})
export class AccountsModule { }
