import { Component, OnInit  } from '@angular/core';

import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import {MatPaginator, MatTableDataSource} from '@angular/material';

import { locale as english } from './i18n/en';
import { locale as arabic } from './i18n/ar';
import { locale as french } from './i18n/fr';
import {OrdersService} from '../../../services/orders/orders.service';
import { fuseAnimations } from '@fuse/animations';
import {PeriodicElement} from '../../accounts/table-accounts/table-accounts.component';
import {Router} from '@angular/router';


@Component({
  selector: 'app-list-orders',
  templateUrl: './list-orders.component.html',
  styleUrls: ['./list-orders.component.scss'], animations : fuseAnimations

})
export class ListOrdersComponent{
    dataSource;
    showSpinner =  true;
    orders : any = [];
    type;
    displayedColumns = ['id', 'total', 'payment', 'status', 'date'];
    /**
     * Constructor
     *
     * @param {FuseTranslationLoaderService} _fuseTranslationLoaderService
     */
    constructor(
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private orderService: OrdersService,
        private router: Router
    )
    {
        this._fuseTranslationLoaderService.loadTranslations(english, french, arabic);
    }

    /**
     * On init
     */
    ngOnInit(): void
    {
        let url = this.router.url.split('/');
        this.type = url[2];
        this.orderService.getOrders().subscribe(resp => {
            ELEMENT_DATA = [];
            this.orders = resp;
            for (let i= this.orders.length - 1 ; i >= 0 ; i--){
                if (this.type === this.orders[i].order_type)
                    ELEMENT_DATA.push({id : this.orders[i].id, uuid : this.orders[i].uuid , total: this.orders[i].amount, payment: this.orders[i].payment_method , status: this.orders[i].status ,date: this.orders[i].created_at  });
            }
            this.dataSource = new MatTableDataSource(ELEMENT_DATA);
            this.showSpinner = false;

        });


    }


}

let ELEMENT_DATA = [];
