import { NgModule } from '@angular/core';
import { ListOrdersComponent } from './list-orders/list-orders.component';
import {RouterModule} from '@angular/router';
import {TranslateModule} from '@ngx-translate/core';
import {FuseSharedModule} from '../../../@fuse/shared.module';
import { NewOrderComponent } from './new-order/new-order.component';
import {AuthGuardService} from '../../services/login/auth-guard.service';
import { MatButtonModule, MatChipsModule, MatFormFieldModule, MatIconModule,MatCardModule, MatInputModule, MatPaginatorModule, MatRippleModule, MatSelectModule, MatSnackBarModule, MatSortModule, MatTableModule, MatTabsModule } from '@angular/material';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MatProgressSpinnerModule } from '@angular/material';
import { DetailsOrderComponent } from './details-order/details-order.component';


const routes = [
    {
        path     : 'orders/achat',
        //canActivate: [AuthGuardService],
        component: ListOrdersComponent
    },
    {
        path     : 'orders/vente',
        //canActivate: [AuthGuardService],
        component: ListOrdersComponent
    },
    {
        path     : 'orders/neworder',
        canActivate: [AuthGuardService],
        component: NewOrderComponent
    },
    {
        path     : 'orders/details-order/:id',
        component: DetailsOrderComponent
    }
];
@NgModule({
  imports: [
      RouterModule.forChild(routes),
      TranslateModule,
      FuseSharedModule,
      MatButtonModule,
      MatChipsModule,
      MatFormFieldModule,
      MatIconModule,
      MatInputModule,
      MatPaginatorModule,
      MatRippleModule,
      MatCardModule,
      MatSelectModule,
      MatSortModule,
      MatSnackBarModule,
      MatTableModule,
      MatTabsModule,
      BrowserAnimationsModule,
      MatProgressSpinnerModule
  ],
  declarations: [
      ListOrdersComponent,
      NewOrderComponent,
      DetailsOrderComponent,
  ],
    entryComponents: [

    ],
})
export class OrdersModule { }
