module.exports = "/**\n * Applies styles for users in high contrast mode. Note that this only applies\n * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`\n * attribute, however Chrome handles high contrast differently.\n * @param target Which kind of high contrast setting to target. Defaults to `active`, can be\n *    `white-on-black` or `black-on-white`.\n */\n/* Theme for the ripple elements.*/\n/* stylelint-disable material/no-prefixes */\n/* stylelint-enable */\napp-layout {\n  display: flex;\n  flex: 1 1 auto;\n  width: 100%;\n  height: 100%; }\napp-layout #main {\n    position: relative;\n    display: flex;\n    flex: 1 1 auto;\n    flex-direction: column;\n    width: 100%;\n    height: 100%;\n    z-index: 1;\n    min-width: 0; }\napp-layout #main > .container {\n      position: relative;\n      display: flex;\n      flex: 1 1 0%;\n      width: 100%;\n      min-height: 0;\n      min-width: 0; }\napp-layout #main > .container > .container {\n        position: relative;\n        display: flex;\n        flex: 1 1 0%;\n        flex-direction: column;\n        min-width: 0; }\napp-layout #main > .container > .container > .container {\n          position: relative;\n          display: flex;\n          flex: 1 1 0%;\n          flex-direction: column;\n          -webkit-transform: translateZ(0);\n                  transform: translateZ(0);\n          overflow-x: hidden;\n          overflow-y: auto;\n          -webkit-overflow-scrolling: touch; }\napp-layout #main > .container > .container > .container content.inner-scroll {\n            flex: 1 1 0%;\n            min-height: 0; }\napp-layout #main > .container > .container > .container content.inner-scroll > *:not(router-outlet) {\n              flex: 1 1 0%; }\n"