import { Injectable } from '@angular/core';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {Router} from '@angular/router';

@Injectable({
  providedIn: 'root'
})
export class AccountsService {
    api_url = 'https://smartstock.elbanyaoui.com/api/accounts';
    myToken = localStorage.getItem('token');
    headers = new HttpHeaders({ 'Authorization': 'Bearer ' + this.myToken });
    accounts = [];


    constructor(private httpClient: HttpClient , private router: Router) { }

       getListAccounts(){
      const httpOptions = {
          headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': 'Bearer ' + this.myToken })};
           return this.httpClient.get(
            this.api_url,  httpOptions
        );

      }

  addNewAccount(object){
      this.httpClient.post(this.api_url, object,{ headers: this.headers }).subscribe( (resp: any) => {
            if(resp.status)
                this.router.navigate(['/accounts']);
          }
      );

  }
    getSingleAccount(uuid){
        return this.httpClient.get(this.api_url+"/"+uuid,{ headers: this.headers });

    }

    deleteAccount(uuid){
        this.httpClient.delete(this.api_url + "/"+ uuid,{ headers: this.headers }).subscribe( (resp: any) => {
                console.log(resp);
            }
        );

    }

}
