import { Component, OnInit } from '@angular/core';
import { StocksService } from '../../../services/stocks/stocks.service';
import { FuseTranslationLoaderService } from '@fuse/services/translation-loader.service';
import {FuseConfirmDialogComponent} from '@fuse/components/confirm-dialog/confirm-dialog.component';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';


import { locale as english } from './i18n/en';
import { locale as arabic } from './i18n/ar';
import { locale as french } from './i18n/fr';
import {AccountsService} from '../../../services/accounts/accounts.service';
import {OrdersService} from '../../../services/orders/orders.service';
import {CategoriesService} from '../../../services/categories/categories.service';
import {ItemsService} from '../../../services/items/items.service';


@Component({
  selector: 'app-new-order',
  templateUrl: './new-order.component.html',
  styleUrls: ['./new-order.component.scss']
})
export class NewOrderComponent implements OnInit{
    animal: string;
    name: string;
   stocks: any = [];
   accounts: any = [];
    orderTarget;
    textArray = [
        'deep-orange-50-bg',
        'teal-bg',
        'cyan-900-bg',
        'fuse-navy-bg'

    ];
    showAddingCardValue = false ;
    randomColorr;
   stockName;
   accountName;
   payementMethod = 'cash';
   orderLine:any = [];
   items=[];
   qte;
   price;
   unity;
   total;
    showSpinner = true ;
   categories;
    constructor(
        private _fuseTranslationLoaderService: FuseTranslationLoaderService,
        private stock: StocksService,
        private account: AccountsService,
        private order: OrdersService,
        private categorie : CategoriesService,
        private item: ItemsService,
        public dialog: MatDialog
    )
    {
        this._fuseTranslationLoaderService.loadTranslations(english, french, arabic);
    }

    openDialog(): void {
        const dialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            width: '250px',
            data: {name: this.name, animal: this.animal}
        });

        dialogRef.afterClosed().subscribe(result => {
            console.log('The dialog was closed');
            this.animal = result;
        });
    }


    ngOnInit(){
        this.randomColor();
        this.stock.getStocks().subscribe( resp => {
            this.stocks = resp ;
            this.stocks = this.stocks.stocks;
        });

        this.account.getListAccounts().subscribe(
            resp => {
                this.accounts = resp ;
            }
        );

        this.categorie.getCategories().subscribe(
            (resp:any) => {
                this.categories = resp.categories ;
                console.log(this.categories);
                this.showSpinner = false ;
            }
        )



    }

    whatValue(){
        console.log(this.payementMethod);
    }

    setStock(e){
        this.stockName = e ;
        console.log(this.stockName);
    }

    setAccount(e){
        this.accountName = e ;
        console.log(this.accountName);
    }


    sendOrder() {

        let body = {
            "stock" : this.stockName,
            "order_type": "achat",
            "account": this.accountName,
            "payment_method": this.payementMethod,
            "order_lines": this.orderLine
        };
        this.order.setOrder(body);

    }

    addItem(){
        this.orderLine.push({
            "item" : {
                'custom_name': this.item,
                'qty': this.qte,
                'price': 0,
                'unity':'kg',
            }
        });
    }

    listItems(uuid){
        console.log(uuid);
        this.item.getItemsList(uuid).subscribe(
            (resp:any) => {
                this.items= resp.items;
            }
        );
    }


    randomColor(){
         this.randomColorr = this.textArray[Math.floor(Math.random()*this.textArray.length)];
    }

    chooseTarget(e){
        this.qte = null;
        this.showAddingCardValue = true ;
        this.orderTarget = e.target.textContent;
    }

    addToOrderLine(){
        let skip =true;
        for(let i=0 ; i<this.orderLine.length ; i++){
            if(this.orderLine[i].custom_name == this.orderTarget){
                this.orderLine[i].qte = this.orderLine[i].qte + this.qte ;
                this.orderLine[i].price = this.price;
                this.orderLine[i].unity = this.unity;
                return;
            }
        }
        this.orderLine.push({qte:this.qte,custom_name:this.orderTarget,unity:this.unity,price:this.price});
        console.log(this.orderLine);
    }

}

